<script>
    import $ from 'jquery'
    import Files from "@/components/Files.vue"
    import CustomSelect from "@/components/CustomSelect.vue"
    export default {
        name: 'CreateTravailleurs',
        components: { CustomSelect,Files },
        props: [
            'gridApi',
            'modalFormId',
        'horairesData',
        'tachesData',
        'usersData',
            ],
        data () {
            return {
                errors: [],
                isLoading: false,
                form: {
                
                id:"",
                
                horaire_id:"",
                
                user_id:"",
                
                lun:"",
                
                mar:"",
                
                mer:"",
                
                jeu:"",
                
                ven:"",
                
                sam:"",
                
                dim:"",
                
                created_at:"",
                
                updated_at:"",
                
                extra_attributes:"",
                
                deleted_at:"",
                
                tache_id:"",
                
                identifiants_sadge:"",
                
                creat_by:"",
                                }
            }
        },
        methods: {
            createLine () {
                this.isLoading = true
                this.axios.post('/api/travailleurs', this.form).then(response => {
                    this.isLoading = false
                    this.resetForm()
                    this.gridApi.applyServerSideTransaction({
                        add: [
                            response.data
                        ],
                    });
                    this.gridApi.refreshServerSide()
                    this.$bvModal.hide(this.modalFormId)
                    this.$toast.success('Operation effectuer avec succes')
                    this.$emit('close')
                    console.log(response.data)
                }).catch(error => {
                    this.errors = error.response.data.errors
                    this.isLoading = false
                    this.$toast.error('Erreur survenue lors de l\'enregistrement')
                })
            },
            resetForm () {
                this.form = {
                                            id:"",
                                            horaire_id:"",
                                            user_id:"",
                                            lun:"",
                                            mar:"",
                                            mer:"",
                                            jeu:"",
                                            ven:"",
                                            sam:"",
                                            dim:"",
                                            created_at:"",
                                            updated_at:"",
                                            extra_attributes:"",
                                            deleted_at:"",
                                            tache_id:"",
                                            identifiants_sadge:"",
                                            creat_by:"",
                                    }
            }
        }
    }
</script>
